import { environment } from '../environment';
import { CookieService } from './CookieService';
import { isSuccessStatusCode, urlJoin } from '../tool';

interface IEnvelope<T>
{
  iKey: string;
  time: string;
  name: string;
  data: T;
}

interface IExceptionData
{
  baseType: string;
  baseData: IExceptionBaseData;
}

interface IExceptionBaseData
{
  ver: number;
  properties: any;
  exceptions: IExceptionModel[]
}

interface IExceptionModel
{
  typeName: string;
  message: string;
  stack: string;
}


class AppInsightsServiceClass
{
  private readonly customEventsVersion = 2;
  private initialized = false;
  
  readonly instrumentationKey = "55e88bf4-37a9-460c-8987-fb67016be741";
  readonly baseUri = "https://dc.services.visualstudio.com";
  
  constructor()
  {
    console.log(process.env.NODE_ENV);
    if(typeof window !== "undefined")
    {
    }
  }
  
  async trackError(message: string, error: Error)
  {
    let model: IEnvelope<IExceptionData> = {
      iKey: this.instrumentationKey,
      name: "ExceptionData",
      time: new Date().toISOString(),
      data: {
        baseType: "ExceptionData",
        baseData: {
          ver : this.customEventsVersion,
          properties: {
            version: this.customEventsVersion,
            message
          },
          exceptions: [
            {
              message: error.message,
              stack: error.stack,
              typeName: error.name
            }
          ]
        }
      }
    }
    
    if(environment.isDev == true)
      return;
      
    let status;
    try
    {
      let res = await fetch(urlJoin(this.baseUri, "v2/track"), {
        method: "post",
        body: JSON.stringify(model)
      });
      status = res.status;
      if(!isSuccessStatusCode(res.status))
        throw new Error();
    }
    catch(err)
    {
      console.error(`Error tracking exception, status: ${status}`);
    }
  }
}


export const AppInsightsService = new AppInsightsServiceClass();
